<template>
    <div style="background:#eaeaea;" class="fill-height" v-if="data">
        <v-col class="text-center" style="position: fixed;z-index: 1;" v-if="data.has_timer != 'No'">
            <v-chip color="primary">{{count}}</v-chip>
        </v-col>
        <v-container>
            <div style="padding-bottom: 50px"></div>
            <v-row justify="center" v-if="loading">
                <v-col sm="12" md="6" class="text-center">
                    <v-progress-circular
                        indeterminate
                        color="primary"
                    ></v-progress-circular>
                </v-col>
            </v-row>
            <v-row justify="center" v-else>
                <v-col sm="12" md="6">
                    <v-row dense>
                        <v-col cols="12">
                            <v-card outlined>
                                <v-card-title class="justify-center">{{contents.name}}</v-card-title>
                            </v-card>
                        </v-col>
                        <v-col cols="12" v-for="post in contents.posts" :key="post.id">
                            <v-card outlined>
                                <v-card-text>
                                    <p class="subtitle" v-html="post.post_content"></p>
                                    <div v-if="data.name == 'CFIT 2'">
                                        <v-text-field maxlength="2" outlined dense placeholder="Masukkan jawabannya.." v-model="answered[post.id+'']"></v-text-field>
                                    </div>
                                    <div v-else>
                                        <template v-if="post.items">
                                        <v-radio-group @change="selectedAnswer(post.id)" dense v-model="answered[post.id+'']">
                                            <v-radio v-for="item in post.items" :key="item.id" :value="item.id" class="d-flex align-items-center">
                                                <template v-slot:label>
                                                    <span v-html="item.post_content" style="width:100%"></span>
                                                </template>
                                            </v-radio>
                                        </v-radio-group>
                                        </template>
                                    </div>
                                </v-card-text>
                            </v-card>
                        </v-col>
                    </v-row>
                    <v-row dense>
                        <v-col class="text-right">
                            <v-btn @click="next" depressed color="primary" block>
                                {{index == categories.length-1 ? 'Selesai' : 'Selanjutnya'}} <v-icon>{{index == categories.length-1 ? 'mdi-check' :'mdi-arrow-right'}}</v-icon>
                            </v-btn>
                            <br>
                            <span>{{loadingtext}}</span>
                        </v-col>
                    </v-row>
                </v-col>
            </v-row>
        </v-container>
    </div>
</template>

<script>
// import Swal from 'sweetalert2'
import { mapGetters } from 'vuex'
export default {
    name:'Sequence',
    data(){
        return{
            exam_id:0,
            loading:true,
            contents:{},
            answered:{},
            loadingtext:'',
            index:0,
            interval:null,
            data:{},
            counts:[],
            count:"00:00:00",
            time:0
        }
    },
    async created(){
        this.exam_id = this.$route.params.id
        await this.$store.dispatch('sequence/fetchCategories',{id:this.exam_id})
        if(this.exam_id == undefined)
        {
            this.$router.push({'name':'dashboard'})
            return
        }
        var lastCategory = JSON.parse(localStorage.getItem("lastCategory"))
        if(lastCategory)
        {
            this.index = this.categories.findIndex(cat => {
                return cat.id == lastCategory.category.id
            })
            this.data = this.categories[this.index]
            // this.data.countdown = lastCategory.time_left
        }
        else
            this.data = this.categories[this.index]
            
        var request = await this.$store.dispatch('sequence/singleCategory',{id:this.data.id})
        if(request.status == 200)
            this.contents = request.data
        
        
        if(this.data.has_timer) // == "Countdown"){
            this.countdown()

        this.loading = false
        // }
    },
    watch:{
        data(val){
            if(val.has_timer == "Countdown"){
                this.counts = val.countdown.split(':')
                this.time = parseInt(this.counts[2])
                this.time += parseInt(this.counts[1])*60
                this.time += (parseInt(this.counts[0])*60)*60
            }else if(val.has_timer == "Ticker"){
                this.counts = val.countdown ? val.countdown.split(':') : [0,0,0]
            }
        }
    },
    computed:{
        ...mapGetters({
            categories : 'sequence/getCategories'
        })
    },
    methods:{
        async selectedAnswer(){
            localStorage.setItem("answered",JSON.stringify(this.answered))
        },
        countdown(){
            if(this.data.has_timer == "Countdown"){
                this.interval = setInterval(()=>{ 

                    if(this.counts[1] < 0 && this.counts[0] > 0){
                        this.counts[0] -= 1
                        this.counts[1] = 59
                    }

                    if(this.counts[2] < 0 && this.counts[1] > 0){
                        this.counts[1] -= 1
                        this.counts[2] = 59
                    }

                    if(this.time == 0){
                        this.next()
                    }else{
                        this.count = `${this.counts[0]}:${this.counts[1]}:${this.counts[2]}`
                    }

                    this.counts[2]--;
                    this.time--;

                    localStorage.setItem('lastCategory',JSON.stringify({
                        time_left:`${this.counts[0]}:${this.counts[1]}:${this.counts[2]}`,
                        category:this.data
                    }))

                },1000)
            }else if(this.data.has_timer == "Ticker"){

                this.interval = setInterval(()=>{
                    if(this.counts[2] > 59){
                        this.counts[1] = parseInt(this.counts[1])+1
                        this.counts[2] = 0
                    }

                    if(this.counts[1] > 59){
                        this.counts[0] = parseInt(this.counts[0])+1
                        this.counts[1] = 0
                    }
                    
                    this.count = `${this.counts[0]}:${this.counts[1]}:${this.counts[2]}`

                    this.counts[2]++;
                    this.time++;

                    localStorage.setItem('lastCategory',JSON.stringify({
                        time_left:`${this.counts[0]}:${this.counts[1]}:${this.counts[2]}`,
                        category:this.data
                    }))
                    
                },1000)
            }
        },
        async next(){
            window.scrollTo({
                top: 0,
                behavior: 'smooth'
            });

            // if(this.data.test_tool != 'TPA' && this.data.posts[0].post_as == 'Soal')
            var condition = false
            if(condition)
            {
                var haveNull = false
                
                // console.log(this.answered)
                for(var i=0;i<this.data.posts.length;i++){
                    if(this.answered[this.data.posts[i].id]==undefined)
                    {
                        haveNull = true
                        break
                    }
                    
                }
                if(haveNull) {
                    alert("Terdapat jawaban yang belum di pilih")
                    return
                }
            }

            this.$store.dispatch('sequence/sendAnswer',{id:this.exam_id,answered:this.answered})
            if(this.index == this.categories.length-1){
                await this.finish()
            }else{
                await clearInterval(this.interval)
                this.index++
                this.data = this.categories[this.index]
                var request = await this.$store.dispatch('sequence/singleCategory',{id:this.data.id})
                if(request.status == 200)
                    this.contents = request.data

                this.countdown()
                localStorage.setItem('lastCategory',JSON.stringify({
                    time_left:`${this.counts[0]}:${this.counts[1]}:${this.counts[2]}`,
                    category:this.data
                }))
            }
        },
        async finish(){ 
            var c = confirm("Apakah anda yakin telah menyelesaikan ujian?")
            if(c)
            {
                this.loadingtext = 'Menunggu...'
                await clearInterval(this.interval)
                await this.$store.dispatch('sequence/finishExam',{id:this.exam_id})
                this.$router.push({name:'finish'})
            }
            
        },
    },
}
</script>

<style>
    p img {
        max-width: 100% !important;
    }
    input[type=text] {
        text-transform: uppercase;
    }
</style>