import axios from 'axios'
export default {
    namespaced: true,
    state:{
        detail:[],
        activeExam:{}
    },
    mutations:{
        setDetail: (state, value) => state.detail = value,
        setActiveExam: (state, value) => state.activeExam = value
    },
    getters:{
        getDetail: (state) => state.detail,
        getActiveExam: (state) => state.activeExam
    },
    actions: {
        putActiveExam({commit},exam){
            commit('setActiveExam',exam)
        },
        detail({commit}){
            return new Promise(function(resolve) {
                var token = window.localStorage.getItem('token')
                axios.get(process.env.VUE_APP_URL+'dashboard', {
                    headers: { 
                        "Authorization": "Bearer "+token 
                    }
                }).then(response => {
                    commit('setDetail',response.data)
                    resolve(response)
                }).catch(error => {
                    resolve(error.response)
                })
            })
        },
        sequences({commit},payload){
            return new Promise(function(resolve) {
                var token = window.localStorage.getItem('token')
                axios.get(process.env.VUE_APP_URL+'dashboard/sequences?exam_id='+payload.id, {
                    headers: { 
                        "Authorization": "Bearer "+token 
                    }
                }).then(response => {
                    resolve(response)
                    commit
                }).catch(error => {
                    resolve(error.response)
                })
            })
        },
    },
};