<template>
    <v-container class="fill-height">
        <v-row dense align="center" justify="center">
            <v-col sm="12" md="5" class="text-center">
                <v-card outlined class="pa-5">
                    <v-card-text>
                        <v-container>
                            <v-row>
                                <v-col cols="6">
                                    <v-img src="../assets/logo.png" alt="Logo TMC" eager height="150" contain></v-img>
                                </v-col>

                                <v-col cols="6">
                                    <v-img src="../assets/partner.jpeg" alt="Logo Partner" eager height="150" contain></v-img>
                                </v-col>
                            </v-row>
                            <v-row>
                                <v-col cols="12">
                                    <h2 class="heading my-5">Login</h2>
                                    <v-alert v-if="err_msg" type="error" dense>{{err_msg}}</v-alert>
                                    <v-alert v-if="success_msg" type="success" dense>{{success_msg}}</v-alert>

                                    <v-form ref="form" @submit.prevent="handleLogin" id="form-login">
                                        <v-text-field dense outlined v-model="username" :loading="loading" :rules="rules" label="Username" />
                                        <v-btn depressed class="primary" type="submit" form="form-login" :loading="loading" block>{{login_label}}</v-btn>
                                        <div>
                                            <br>
                                            <b>Kontak Whatsapp:</b><br>
                                            087867504803 (Bapak Ferdy)<br>
                                            082166601184 (Ibu Thalita)<br>
                                            Telegram: <a href="https://t.me/tmcidsupport">https://t.me/tmcidsupport</a>
                                        </div>
                                        <span>copyright &copy; 2020 Z-Techno</span>
                                    </v-form>
                                </v-col>    
                            </v-row>
                        </v-container>
                    </v-card-text>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    export default {
        name:'Login',
        data(){
            return{
                loading:false,
                login_label:'Login',
                username:'',
                err_msg:'',
                success_msg:'',
                browserCheck:false,
                rules: [
                    v => !!v || 'This field is required',
                ],
            }
        },
        created(){
        },
        methods:{
            async handleLogin(){
                this.resetMsg()
                this.login_label = 'Silahkan Tunggu...'
                var login = await this.$store.dispatch('auth/login',{username:this.username})
                if(login.data != null)
                {
                    this.$store.dispatch('auth/setToken',login.data.auth_key)
                    this.success_msg = 'Login Berhasil'
                    this.$router.push({name:'dashboard'})
                }
                else
                    this.err_msg = 'Login Gagal'
                this.login_label = 'Login'
            },
            resetMsg(){
                this.err_msg = ''
                this.success_msg = ''
            }
        }
    }
</script>

<style lang="scss" scoped>

</style>