import axios from 'axios'
export default {
    namespaced: true,
    state:{
        categories:[],
        category:{},
        categoryDemo:{},
    },
    mutations:{
        setCategories: (state, value) => state.categories = value,
        setCategory: (state, value) => state.category = value,
        setCategoryDemo: (state, value) => state.category = value
    },
    getters:{
        getCategories: (state) => state.categories,
        getCategory: (state) => state.category,
        getCategoryDemo: (state) => state.categoryDemo
    },
    actions: {
        putActiveExam({commit},exam){
            commit('setActiveExam',exam)
        },
        fetchCategories({commit},payload){
            return new Promise(function(resolve) {
                var token = window.localStorage.getItem('token')
                axios.get(process.env.VUE_APP_URL+'sequence/categories?exam_id='+payload.id, {
                    headers: { 
                        "Authorization": "Bearer "+token 
                    }
                }).then(response => {
                    commit('setCategories',response.data)
                    resolve(response)
                }).catch(error => {
                    resolve(error.response)
                })
            })
        },
        singleCategory({commit},payload){
            return new Promise(function(resolve) {
                var token = window.localStorage.getItem('token')
                axios.get(process.env.VUE_APP_URL+'sequence/single-categories?category_id='+payload.id, {
                    headers: { 
                        "Authorization": "Bearer "+token 
                    }
                }).then(response => {
                    commit('setCategory',response.data)
                    resolve(response)
                }).catch(error => {
                    resolve(error.response)
                })
            })
        },
        singleCategoryDemo({commit},payload){
            return new Promise(function(resolve) {
                var token = window.localStorage.getItem('token')
                axios.get(process.env.VUE_APP_URL+'sequence/single-categories-demo?category_id='+payload.id, {
                    headers: { 
                        "Authorization": "Bearer "+token 
                    }
                }).then(response => {
                    commit('setCategoryDemo',response.data)
                    resolve(response)
                }).catch(error => {
                    resolve(error.response)
                })
            })
        },
        startExam({commit},payload){
            return new Promise(function(resolve) {
                var token = window.localStorage.getItem('token')
                axios.post(process.env.VUE_APP_URL+'sequence/start', payload, {
                    headers: { 
                        "Authorization": "Bearer "+token 
                    }
                }).then(response => {
                    resolve(response)
                    commit
                }).catch(error => {
                    resolve(error.response)
                })
            })
        },
        finishExam({commit},payload){
            return new Promise(function(resolve) {
                var token = window.localStorage.getItem('token')
                axios.post(process.env.VUE_APP_URL+'sequence/finish', payload, {
                    headers: { 
                        "Authorization": "Bearer "+token 
                    }
                }).then(response => {
                    resolve(response)
                    commit
                }).catch(error => {
                    resolve(error.response)
                })
            })
        },
        sendAnswer({commit},payload){
            return new Promise(function(resolve) {
                var token = window.localStorage.getItem('token')
                axios.post(process.env.VUE_APP_URL+'sequence/send-answer', payload, {
                    headers: { 
                        "Authorization": "Bearer "+token 
                    }
                }).then(response => {
                    resolve(response)
                    commit
                }).catch(error => {
                    resolve(error.response)
                })
            })
        }
    },
};