<template>
    <v-container fluid class="fill-height">
        <v-row justify="center" dense>
            <v-col class="text-center" sm="12" md="4">
                <v-card outlined class="py-5">
                    <v-card-text>
                        <v-avatar color="grey" size="200">
                            <v-img src="https://w5insight.com/wp-content/uploads/2014/07/placeholder-user-400x400.png"/>
                        </v-avatar>
                        <div class="my-5">
                            <h2 class="title">{{participant.name}}</h2>
                            <span class="subtitle-1">ID/Username : {{participant.id_number}}</span>
                        </div>
                        <!-- <v-btn depressed color="warning" block @click="$router.push({name:'start-demo'})" style="margin-bottom:10px">Uji Coba</v-btn> -->
                        <!-- <v-btn depressed color="red" block @click="redirectTo('tutorial')" style="margin-bottom:10px">Tutorial</v-btn> -->
                        <!-- <v-btn depressed color="primary" block v-if="download" @click="redirectTo('download')" style="margin-bottom:10px">Download Laporan</v-btn> -->
                        <!-- <template v-if="detail">
                            <v-btn depressed color="success" block @click="start" v-if="detail.exam && !detail.examParticipant.status">Mulai Tes</v-btn>
                            <v-btn depressed color="success" block @click="lanjut" v-else-if="detail.exam && detail.examParticipant.status == 'start'">Lanjutkan Tes</v-btn>
                            <v-btn text color="success" block v-else-if="detail.exam && detail.examParticipant.status == 'finish'">Anda sudah menyelesaikan tes</v-btn>
                            <v-btn text color="warning" block v-else>Tidak ada tes</v-btn>
                        </template>
                        <template v-else>
                            <v-btn text color="warning" block>Tidak ada tes</v-btn>
                        </template> -->
                        <v-btn text color="red" @click="logout()" block>Keluar</v-btn>
                    </v-card-text>
                </v-card>
            </v-col>
            <v-col sm="12" md="8">
                <v-card outlined class="py-5" v-if="!exams">
                    <v-card-text>
                        <h2><i>Tidak ada Ujian</i></h2>
                    </v-card-text>
                </v-card>
                <v-card outlined class="py-5" v-for="(exam,i) in exams" :key="i">
                    <v-card-text>
                        <h2>{{exam.exam.name}}</h2>
                        <p>Waktu : {{exam.exam.start_time}} - {{exam.exam.end_time}}</p>
                        <v-btn depressed color="warning" @click="mulai(exam.exam,1)" style="margin-bottom:10px;margin-right:10px;">Uji Coba</v-btn>
                        <v-btn depressed color="red" @click="redirectTo(exam.exam.tutorial)" style="margin-bottom:10px;margin-right:10px;">Tutorial</v-btn>
                        <!-- <v-btn depressed color="primary" style="margin-bottom:10px;margin-right:10px;">Download Hasil</v-btn> -->
                        <v-btn depressed color="success" style="margin-bottom:10px;margin-right:10px;" v-if="exam.status == null && exam.exam.in_time" @click="mulai(exam.exam)">Mulai Tes</v-btn>
                        <v-btn depressed color="success" style="margin-bottom:10px;margin-right:10px;" v-else-if="exam.status == 'start'" @click="lanjutkan(exam.exam_id)">Lanjutkan Tes</v-btn>
                        <v-btn text color="success" style="margin-bottom:10px;margin-right:10px;" v-else-if="exam.status == 'finish'">Kamu Telah Menyelesaikan Tes</v-btn>
                    </v-card-text>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
export default {
    name: 'Dashboard',
    data(){
        return{
            exams:[],
            participant:{}
        }
    },
    async created(){
        var request = await this.$store.dispatch('dashboard/detail')
        if(request.status == 200)
        {
            this.exams = request.data.exams
            this.participant = request.data.participant
        }
        else
            this.logout()
    },
    methods:{
        logout(){
            this.$store.dispatch('auth/logout')
            this.$router.push({'name':'login'})
        },
        redirectTo(url){
            window.open(url)
        },
        lanjutkan(id){
            this.$router.push({
                'name':'sequence',
                'params':{
                    'id':id
                }
            })
        },
        mulai(exam,ujicoba=false){
            this.$store.dispatch('dashboard/putActiveExam',exam)
            if(ujicoba)
            this.$router.push({
                'name':'start-demo',
                'params':{
                    'id':exam.id
                }
            })
            else
            this.$router.push({
                'name':'start',
                'params':{
                    'id':exam.id
                }
            })
        }
    }
}
</script>
